body {
  font: var(--font-text);
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.container {
  max-width: 1320px;
  padding: 0 15px;
  margin-left: auto;
}

.grid {
  display: grid;
  grid-template-columns: repeat(11, 1fr);
  grid-gap: var(--grid-gap);
}

.big__container {
  margin: 0 auto;
  max-width: 1410px;
}

.btn {
  padding: 22px 42px;
  border-radius: 30px;
  font-size: 18px;
  line-height: 1;
  font-weight: 300;
  color: var(--color-white);
  transition: background-color 0.2s ease-in-out;
  background-color: var(--color-gigas);
}

.btn:hover {
  background-color: var(--color-gigas-hover);
}

.btn--primary {
  background-color: var(--color-bahama-blue);
}

.btn--primary:hover {
  background-color: var(--color-bahama-blue-hover);
}

.logo {
  display: block;
  width: 219px;
  height: auto;
}

.img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.br {
  width: 100%;
  height: 3px;
  border-radius: 1.5px;
  background-color: #503495;
  opacity: 0.1;
}

.main {
  overflow: hidden;
}

.visually-hidden {
  pointer-events: none;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

:root {
  --color-white: #fff;
  --color-black: #000;
  --color-gigas: #be51d5;
  --color-gigas-hover: #be51d9;
  --color-bahama-blue: #005695;
  --color-bahama-blue-hover: #005999;
  --color-placeholder: #b0bec5;
  --grid-gap: 30px;
  --main-offset: 20px;
  --font-family-bold: 'Montserrat-Bold', sans-serif;
  --font-family-light: 'Montserrat-Light', sans-serif;
  --font-medium-header: 700 30px / 120% var(--font-family-bold);
  --font-links: 700 14px / 120% var(--font-family-bold);
  --font-text: 300 20px / 180% var(--font-family-light);
  --font-title: 700 40px / 180% var(--font-family-bold);
  --font-smalltitle: 700 30px / 180% var(--font-family-bold);
}

@font-face {
  font-family: "Montserrat-Light";
  src: url("../fonts/Montserrat-Light.woff2") format("woff2"), url("../Montserrat-Light.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat-Bold";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../Montserrat-Bold.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  padding: 10px 0;
  background-color: var(--color-bahama-blue);
}

.header__container {
  position: relative;
  display: flex;
  align-items: center;
}

.header__right {
  margin-left: auto;
  display: flex;
  align-items: center;
}

@media screen and (max-width: 1000px) {
  .logo {
    width: 150px;
  }
}

.burger__icon {
  position: relative;
  display: block;
  width: 30px;
  height: 14px;
}

.burger__icon::before, .burger__icon::after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  height: 2px;
  width: 30px;
  background-color: var(--color-white);
  transition: transform 0.2s ease-in-out;
}

.burger__icon::before {
  top: 0;
}

.burger__icon::after {
  bottom: 0;
}

.burger.open .burger__icon::after {
  transform: rotate(45deg) translate(-8px, -1px);
}

.burger.open .burger__icon::before {
  transform: rotate(-45deg) translate(-8px, 0);
}

.burger__nav {
  display: none;
  position: absolute;
  top: 65px;
  left: 0;
  width: 100%;
  opacity: 0;
  transform: translateY(-20px);
  background-color: var(--color-bahama-blue);
  justify-content: center;
  transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
}

.burger.open .burger__nav {
  display: block;
  opacity: 1;
  transform: translateY(0px);
  transition: transform 0.2s ease-in-out;
}

.burger.open .nav__list {
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.burger.open .nav__item {
  margin-right: 0;
  margin-bottom: 20px;
}

.nav__list {
  display: flex;
  align-items: center;
}

.nav__item {
  font: var(--font-links);
  color: var(--color-white);
  text-transform: uppercase;
  opacity: 0.5;
  margin-right: 75px;
  letter-spacing: 2.7px;
}

.nav__item a {
  pointer-events: none;
}

.nav__item:hover {
  opacity: 1;
}

.nav__item-active {
  opacity: 1;
}

@media screen and (max-width: 1300px) {
  .nav__item {
    font-size: 13px;
    margin-right: 50px;
  }
}

.hero {
  margin-bottom: 182px;
}

.hero__container {
  position: relative;
  z-index: 2;
  padding-top: 150px;
}

.hero__inner {
  display: flex;
  margin-bottom: 20px;
  align-items: center;
  justify-content: space-between;
}

.hero__left {
  margin-top: 72px;
  width: 47%;
}

.hero__title {
  font-size: 45px;
  font-weight: 700;
  line-height: 60px;
  color: var(--color-white);
  margin-bottom: 43px;
}

.hero__text {
  color: var(--color-white);
  font: var(--font-text);
}

.hero__right {
  width: 51%;
}

.hero__bg {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1;
}

.hero__form {
  margin: 0 auto;
  width: 100%;
}

@media screen and (max-width: 1750px) {
  .hero__text {
    color: var(--color-black);
  }
}

@media screen and (max-width: 1500px) {
  .hero__inner {
    display: block;
  }
  .hero__left {
    width: 65%;
    margin-bottom: 60px;
  }
  .hero__right {
    width: 40%;
    position: absolute;
    top: 35%;
    z-index: 0;
    right: 0;
  }
}

@media screen and (max-width: 1300px) {
  .hero__container {
    padding-top: 150px;
  }
  .hero__inner {
    display: block;
  }
  .hero__left {
    text-align: center;
    width: 70%;
    margin: 0 auto;
    margin-bottom: 60px;
  }
  .hero__right {
    display: none;
  }
  .hero__title {
    color: var(--color-black);
  }
  .hero__form {
    margin: 0 auto;
    width: 100%;
  }
}

@media screen and (max-width: 1000px) {
  .hero {
    margin-top: 80px;
    margin-bottom: 90px;
  }
  .hero__container {
    padding-top: 0;
  }
  .hero__title {
    font-size: 30px;
    line-height: 1.2;
  }
  .hero__text {
    font-size: 15px;
  }
}

@media screen and (max-width: 700px) {
  .hero__title {
    font-size: 25px;
    line-height: 1.2;
    margin-bottom: 25px;
  }
  .hero__text {
    font-size: 13px;
  }
}

.form {
  position: relative;
  z-index: 100;
  display: flex;
  align-items: center;
  margin: 0 auto;
  max-width: 690px;
  padding: 30px 47px 30px 72px;
  border-radius: 5px;
  background-color: var(--color-white);
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

.form__label-mess {
  display: flex;
  align-items: center;
}

.form__span-mess {
  margin-right: 25px;
}

.form__span-mess svg {
  width: 20px;
  height: 15px;
  fill: #b0bec5;
}

.form__input-mess {
  color: var(--color-placeholder);
  font: var(--font-text);
  outline: none;
  width: 375px;
  padding-right: 20px;
  border: none;
}

.form__input-mess::placeholder {
  font: var(--font-text);
  color: var(--color-placeholder);
  font-size: 18px;
}

.form__btn {
  letter-spacing: 1px;
  white-space: nowrap;
  padding: 22px 34px;
}

@media screen and (max-width: 1000px) {
  .form {
    max-width: 500px;
    padding: 15px 20px 17px 30px;
  }
  .form__input-mess {
    max-width: 280px;
  }
  .form__btn {
    padding: 14px 24px;
  }
}

@media screen and (max-width: 700px) {
  .form__input-mess::placeholder {
    font-size: 13px;
  }
  .form__btn {
    padding: 14px 24px;
    font-size: 15px;
  }
}

@media screen and (max-width: 550px) {
  .form__input-mess {
    max-width: 180px;
  }
  .form__input-mess::placeholder {
    font-size: 13px;
  }
  .form__btn {
    padding: 8px 10px;
    font-size: 13px;
  }
  .form__span-mess {
    margin-right: 10px;
  }
}

@media screen and (max-width: 450px) {
  .form {
    padding: 10px;
    font-size: 10px;
  }
  .form__span-mess {
    display: none;
  }
  .form__input-mess {
    font-size: 10px;
  }
  .form__input-mess::placeholder {
    font-size: 10px;
  }
  .form__btn {
    margin-left: auto;
    font-size: 10px;
  }
}

.salesapp {
  margin-bottom: 257px;
  position: relative;
  width: 100%;
}

.salesapp__bg {
  position: absolute;
  top: -86px;
  left: 0;
  z-index: -1;
  width: 503px;
}

.salesapp__left {
  grid-column: 1 / 5;
  padding-top: 18px;
}

.salesapp__right {
  grid-column: 6 / -1;
}

.salesapp__title {
  font-size: 36px;
  font-weight: 700;
  line-height: 60px;
  margin-bottom: 20px;
}

.salesapp__text-inner {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  margin-bottom: 59px;
}

.salesapp__text {
  grid-column: 5 span;
  letter-spacing: 0.4px;
  color: #546e7a;
  font-size: 20px;
  font-weight: 300;
}

.salesapp__text:not(:last-child) {
  margin-bottom: 36px;
}

.salesapp__btn {
  display: inline-block;
  color: #553597;
  font-size: 18px;
  font-weight: 700;
  margin-right: 10px;
  letter-spacing: 0.6px;
}

.salesapp__btn svg {
  width: 35px;
  height: 13px;
  fill: #553597;
}

@media screen and (max-width: 1200px) {
  .salesapp__text-inner {
    display: block;
  }
  .salesapp__right {
    position: relative;
    z-index: 2;
  }
}

@media screen and (max-width: 1000px) {
  .salesapp {
    margin-bottom: 110px;
  }
  .salesapp__left {
    grid-column: 11 span;
    grid-row: 2;
  }
  .salesapp__right {
    grid-column: 2 / 9 span;
    text-align: center;
  }
  .salesapp__title {
    font-size: 30px;
    line-height: 1.2;
  }
  .salesapp__text {
    font-size: 15px;
  }
  .salesapp__text:not(:last-child) {
    margin-bottom: 20px;
  }
}

@media screen and (max-width: 1000px) {
  .salesapp__title {
    font-size: 30px;
    line-height: 1.2;
    margin-bottom: 25px;
  }
  .salesapp__text {
    font-size: 13px;
  }
}

.app {
  max-width: 435px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  position: relative;
  z-index: 1;
}

.app__slider {
  position: relative;
  z-index: -2;
  padding: 35px 36px 7px 28px;
  border-radius: 5px 5px 0 0;
  background-image: url("../img/app/man.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: 1px solid var(--color-black);
}

.app__slider::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  border-radius: 5px 5px 0 0;
  background-color: #78909c;
  opacity: 0.3;
}

.app__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 162px;
}

.app__shop svg {
  width: 20px;
  height: 19px;
  fill: var(--color-white);
}

.app__menu {
  width: 56px;
  height: 56px;
  padding: 15px;
  border: 3px solid #e1e5e8;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.app__menu span {
  width: 15px;
  height: 10px;
  display: block;
  position: relative;
}

.app__menu span::before, .app__menu span::after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  height: 3px;
  width: 15px;
  background-color: var(--color-white);
  left: 0;
  transition: width 0.2s ease-in-out;
}

.app__menu span::before {
  top: 0;
}

.app__menu span::after {
  bottom: 0;
}

.app__menu span:hover::after {
  width: 30px;
}

.app__title {
  font: var(--font-text);
  font-size: 48px;
  line-height: 36px;
  margin-bottom: 12px;
  color: var(--color-white);
}

.app__footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.app__count__all {
  font-size: 18px;
  font-weight: 700;
  color: var(--color-white);
}

.app__count {
  display: flex;
  align-items: center;
}

.app__count-element {
  display: block;
  width: 5px;
  height: 5px;
  border-radius: 100%;
  opacity: 0.5;
  background-color: var(--color-white);
}

.app__count-element:not(:last-child) {
  margin-right: 7px;
}

.app__count-element.active {
  display: block;
  width: 10px;
  height: 10px;
  opacity: 1;
}

.app__descr {
  padding: 50px 67px 48px;
  background-color: var(--color-white);
}

.app__btn {
  display: block;
  position: relative;
  font: var(--font-links);
  font-size: 24px;
  padding: 27px 70px;
  border: 3px solid #eceff1;
  border-radius: 5px;
  color: #455a64;
}

.app__btn span {
  display: block;
  position: absolute;
  bottom: -40px;
  right: -50px;
  z-index: 1;
  width: 100px;
  height: 100px;
  background-color: #d9f2fc;
  border: 2px solid #75d1f5;
  border-radius: 100%;
}

.app__btn span::before {
  content: '';
  position: absolute;
  display: block;
  width: 40px;
  height: 40px;
  top: 50%;
  left: 50%;
  z-index: 2;
  background-color: #75d1f5;
  transform: translate(-50%, -50%);
  border-radius: 100%;
}

@media screen and (max-width: 1000px) {
  .app {
    margin: 0 auto;
  }
}

@media screen and (max-width: 700px) {
  .app {
    max-width: 435px;
