.burger {
  &__icon {
    position: relative;
    display: block;
    width: 30px;
    height: 14px;

    &::before,
    &::after {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      height: 2px;
      width: 30px;
      background-color: var(--color-white);
      transition: transform 0.2s ease-in-out;
    }

    &::before {
      top: 0;
    }
    &::after {
      bottom: 0;
    }
  }

  &.open &__icon {
    &::after {
      transform: rotate(45deg) translate(-8px, -1px);
    }

    &::before {
      transform: rotate(-45deg) translate(-8px, 0);
    }
  }

  &__nav {
    display: none;
    position: absolute;
    top: 65px;
    left: 0;
    width: 100%;
    opacity: 0;
    transform: translateY(-20px);
    background-color: var(--color-bahama-blue);
    justify-content: center;
    transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
  }

  &.open &__nav {
    display: block;
    opacity: 1;
    transform: translateY(0px);
    transition: transform 0.2s ease-in-out;
  }

  &.open .nav__list {
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &.open .nav__item {
    margin-right: 0;
    margin-bottom: 20px;
  }
}
