.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  padding: 10px 0;
  background-color: var(--color-bahama-blue);

  &__container {
    position: relative;
    display: flex;
    align-items: center;
  }

  &__right {
    margin-left: auto;
    display: flex;
    align-items: center;
  }
}

@media screen and (max-width: 1000px) {
  .logo {
    width: 150px;
  }
}
